import React, {Component} from "react"
import { Link } from 'react-router-dom';

import logo1 from '../../assets/images/logo.png';
import logo2 from '../../assets/images/logo@2x.png';

class Header extends Component {
	render() {
		return (
			<div>
				<div id="top-bar">
					<div className="container clearfix">
						<div className="col_half nobottommargin clearfix">
							<div id="top-social">
								<ul>
									<li><a href="https://www.facebook.com/Mikdanjey" target={"_blank"} className="si-facebook"><span className="ts-icon"><i
										className="icon-facebook"/></span><span className="ts-text">Facebook</span></a>
									</li>
									<li><a href="https://twitter.com/mikdanjey" target={"_blank"} className="si-twitter"><span className="ts-icon"><i
										className="icon-twitter"/></span><span className="ts-text">Twitter</span></a>
									</li>
									<li><a href="https://github.com/mikdanjey" target={"_blank"} className="si-github"><span className="ts-icon"><i
										className="icon-github-circled"/></span><span className="ts-text">Github</span></a>
									</li>
								</ul>
							</div>
						</div>
					</div>
				</div>
				<header id="header" className="sticky-style-2">
					<div className="container clearfix">
						<div id="logo">
							<Link to={"/"} className="standard-logo"><img src={logo1} alt="Canvas Logo"/></Link>
							<Link to={"/"} className="retina-logo"><img src={logo2} alt="Canvas Logo"/></Link>
						</div>
						<ul className="header-extras">
							<li>
								<i className="i-plain icon-call nomargin"/>
								<div className="he-text">
                                    Call Us
									<a href="tel:+917200339195"><span>(+91) 72 00 33 91 95</span></a>
								</div>
							</li>
							<li>
								<i className="i-plain icon-line2-envelope nomargin"/>
								<div className="he-text">
                                    Email Us
									<a href="mailto:mikdan.solution@gmail.com"><span>mikdan.solution@gmail.com</span></a>
								</div>
							</li>
						</ul>
					</div>
					<div id="header-wrap">
						<nav id="primary-menu" className="with-arrows style-2 center">
							<div className="container clearfix">
								<div id="primary-menu-trigger"><i className="icon-reorder"/></div>
								<ul>
									<li className={(this.props.currentPage === "homes")? "current" : ""}><Link to={"/"}><div>{this.props.currentPage}</div></Link></li>
									<li><Link to={"/contact"}><div>Contact</div></Link></li>
									<li><Link to={"/about"}><div>About Us</div></Link></li>
								</ul>
							</div>
						</nav>
					</div>
				</header>
			</div>
		)
	}
}

export default Header