import React, {Component} from "react"
import { Link } from 'react-router-dom';
class Footer extends Component {
	render() {
		return (
			<footer id="footer" className="dark">
				<div id="copyrights">
					<div className="container clearfix">

						<div className="col_half">
                        Copyrights &copy; 2018 All Rights Reserved by Mikdan Tech Solutions.<br />
							<div className="copyright-links"><Link to={'/'}>Terms of Use</Link> / <Link to={'/'}>Privacy Policy</Link>
							</div>
						</div>
						<div className="col_half col_last tright">
							<div className="copyrights-menu copyright-links clearfix">
								<a href="/">Home</a>/<Link to={'/'}>About Us</Link>/<Link to={"/contact"}>Contact</Link>
							</div>
						</div>
					</div>
				</div>
			</footer>
		)
	}
}

export default Footer